@keyframes time-mover {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.clock {
  background-color: gray;
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.clock__center {
  z-index: 1;
  background-color: red;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.clock__hand-big {
  transform-origin: bottom;
  background-color: #00f;
  width: 1.4vmin;
  height: 40vmin;
  animation: 5s linear infinite time-mover;
  position: absolute;
  top: 0;
  left: 39.3vmin;
}

.clock__hand-small {
  transform-origin: bottom;
  background-color: #ff0;
  width: 2vmin;
  height: 20vmin;
  animation: 60s linear infinite time-mover;
  position: absolute;
  top: 20vmin;
  left: 39vmin;
}

body {
  justify-content: center;
  display: flex;
}

